.list__dummy {
  text-align: center;
  text-shadow: 0px 1px 0px white; }

.list__header {
  font-size: 16px;
  display: grid;
  padding: 15px;
  padding-bottom: 5px;
  grid-gap: 30px;
  grid-template-columns: 106px repeat(4, 1fr) 64px; }
  .list__header div:first-child {
    grid-column-start: 2; }
  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    .list__header {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: justify;
          justify-content: space-between; }
      .list__header div:first-child {
        margin-left: 136px; }
      .list__header div:last-child {
        margin-right: 94px; } }
  @media screen and (max-width: 578px) {
    .list__header {
      display: none; } }

.list__songs {
  padding: 0;
  margin: 0px;
  list-style: none; }



